import "./questions.scss";
import Title from "../Title/Title";
import Container from "../Container/Container";
import Accordion from "../Accordion/Accordion";

const data = [
  {
    id: 1,
    title:
      ":      ?",
    descr:
      " ,              .   :            .",
  },
  {
    id: 2,
    title: "   -    ?",
    descr:
      " ,              .   :            .",
  },
  {
    id: 3,
    title: "    ?",
    descr:
      " ,              .   :            .",
  },
  {
    id: 4,
    title: " ,      ?",
    descr:
      " ,              .   :            .",
  },
  {
    id: 5,
    title:
      "           ?",
    descr:
      " ,              .   :            .",
  },
  {
    id: 6,
    title:
      "         ?",
    descr:
      " ,              .   :            .",
  },
  {
    id: 7,
    title: "    ?",
    descr:
      " ,              .   :            .",
  },
];

function Questions() {
  return (
    <div className="questions" id="questions">
      <Container>
        <Title title="  " />
        <div className="questions_items">
          {data.map((item) => {
            return (
              <Accordion title={item.title} descr={item.descr} key={item.id} />
            );
          })}
        </div>
      </Container>
    </div>
  );
}

export default Questions;
