import Container from "../Container/Container";
import InfoItem from "../InfoItem/InfoItem";
import "./infoBlock.scss";

const data = [
  {
    id: 1,
    title: "  ",
    descr:
      "      7000 .                      7000 .               ",
  },
  {
    id: 2,
    title: "  ",
    descr:
      "      7000 .                      7000 .                     7000 .               ",
  },
  {
    id: 3,
    title: "  ",
    descr:
      "      7000 .                      7000 .                     7000 .               ",
  },
  {
    id: 4,
    title: "  ",
    descr:
      "      7000 .                      7000 .               ",
  },
];

function InfoBlock() {
  return (
    <div className="info">
      <Container>
        <div className="info_items">
          {data.map((item) => {
            return (
              <InfoItem descr={item.descr} title={item.title} key={item.id} />
            );
          })}
        </div>
      </Container>
    </div>
  );
}

export default InfoBlock;
